<template>
  <button @click="handleAddTask()" class="button">
    {{ btnText }}
  </button>
</template>

 <script>
export default {
  name: "Button",
  props: {
    btnText: String,
    btnBgColor: String,
  },
  methods: {
    handleAddTask() {
      this.$emit("handleAddTaskForm");
    },
  },
};
</script>

 <style>
.button {
  padding: 1rem 3rem;
  margin-right: 2rem;
  border: 1px solid #000;
  color: #000;
  font-family: inherit;
  font-size: 1.5rem;
  transition: all 0.4s ease-in-out;
  border-radius: 0.5rem;
  text-transform: uppercase;
}

.button:hover {
  background: slateblue;
  border: 1px solid #fff;
  color: #fff;
  cursor: pointer;
}
</style>