<template>
  <div class="tasks">
    <h2>Tasks List</h2>
    <div
      v-if="tasks.length > 0"
      :key="task.id"
      v-for="task in tasks"
      class="tasks__wrapper"
    >
      <Task
        @deleteTask="$emit('deleteTask', task.id)"
        @toggleRemainder="$emit('toggleRemainder', task.id)"
        :task="task"
      />
    </div>
    <h1 v-else="tasks.length < 0" class="no__results">NO TASKS TO DISPLAY !</h1>
  </div>
</template>

<script>
import Task from "./Task";
export default {
  name: "Tasks",
  props: {
    tasks: Array,
  },
  components: {
    Task,
  },
  emits: ["deleteTask", "toggleRemainder"],// if we are emmitting any functions from children
};
</script>
<style>
.tasks {
  border: 2px solid #e0dddd;
  display: grid;
  margin: 4rem auto;
  width: 60%;
  border-radius: 0.5rem;
}
.tasks h2 {
  text-align: center;
  padding-bottom: 1rem;
  border-bottom: 2px solid #e0dddd;
}
.tasks__wrapper {
  padding: 1rem 1rem;
}
.no__results {
  text-align: center;
  color: rgb(238, 50, 50);
}
</style>
