<template>
  <Header
    title="CRUD-VUE"
    @handleAddTaskForm="handleAddTaskForm"
    :showAddTaskForm="showAddTaskForm"
  />
  <AddTask @handleAddTask="handleAddTask" :showAddTaskForm="showAddTaskForm" />
  <Tasks
    :tasks="tasks"
    @deleteTask="deleteTask"
    @toggleRemainder="toggleRemainder"
  />
</template>

<script>
import Header from "./components/Header";
import Tasks from "./components/Tasks";
import AddTask from "./components/AddTask";
import { mock } from "./assets/mockData";

export default {
  name: "App",
  components: {
    Header,
    Tasks,
    AddTask,
  },
  data() {
    return {
      tasks: [],
      showAddTaskForm: false,
    };
  },
  methods: {
    deleteTask(id) {
      this.tasks = this.tasks.filter((ele) => ele.id !== id);
    },
    toggleRemainder(id) {
      this.tasks = this.tasks.map((item) =>
        item.id === id ? { ...item, reminder: !item.reminder } : item
      );
    },
    handleAddTask(task) {
      this.tasks = [...this.tasks, task];
    },
    handleAddTaskForm() {
      this.showAddTaskForm = !this.showAddTaskForm;
    },
  },
  created() {
    this.tasks = mock;
  },
};
</script>

