<template >
  <form v-if="showAddTaskForm" @submit="handleSubmit" class="tasks form">
    <h2>Add Task</h2>
    <div class="form-control">
      <label>Task</label>
      <input type="text" name="title" v-model="title" placeholder="Add Task" />
    </div>
    <div class="form-control">
      <label>Day & Time</label>
      <input
        type="text"
        name="date"
        v-model="date"
        placeholder="Add Day & Time"
      />
    </div>
    <div class="form-control form-control-check">
      <label>Set Reminder</label>
      <input type="checkbox" name="reminder" v-model="reminder" />
    </div>
    <div class="form-control">
      <button type="submit" class="button">Submit</button>
    </div>
  </form>
</template>
<script>
export default {
  name: "Add Task",
  data() {
    return {
      title: "",
      date: "",
      reminder: false,
    };
  },
  props: {
    showAddTaskForm: Boolean,
  },
  methods: {
    handleSubmit(e) {
      e.preventDefault();
      if (!this.title) {
        alert("Please enter a title for the task!");
        return;
      }
      const task = {
        id: Math.round(Math.random(0) * 1000000),
        title: this.title,
        date: this.date,
        reminder: this.reminder,
      };
      this.$emit("handleAddTask", task);
      this.title = "";
      this.date = "";
      this.reminder = false;
    },
  },
};
</script>

